:root {
  --primary-dark: #333;
  --secondary-dark: #444;
  --primary-light: #eee;
  --secondary-light: #ddd;
}

* {
  box-sizing: border-box;
}

#root {
  min-height: 100vh;
}

.home-container {
  background: url("../images/poke-background.jpg") no-repeat center center;
  background-size: cover;
  width: 100%;
  min-height: calc(100vh - 96px);
  color: var(--secondary-light);
  letter-spacing: 0.1em;
}

a,
a:hover,
a:focus,
a:active {
  text-decoration: none;
  color: inherit;
}

.left-panel {
  width: 40%;
  text-align: center;
  padding-top: 4em;
}

h1 {
  margin-top: 0;
}

body {
  margin: 0;
  padding: 0;
  position: relative;
}
.nav-bar-wrapper {
  position: sticky;
  top: 0;
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-height: 1vh;
  background-color: #eee;
  color: #444;
  font-size: 25px;
}

.nav-bar-wrapper h3 {
  letter-spacing: 0.05em;
}

.nav-bar {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 40%;
  list-style: none;
}

.nav-bar li {
  color: #444;
  text-decoration: none;
  transition: all 0.2s ease-in-out;
}

.nav-bar li:hover {
  transform: translateY(-5px);
}

.pokemon-container {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-gap: 20px 20px;
  margin: 40px;
  padding: 0;
}

.pokemon-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 1px solid var(--secondary-light);
  transition: all 0.2s ease-in-out;
}

.pokemon-wrapper:hover {
  transform: scale(1.1);
}

.pokemon-wrapper li {
  list-style: none;
  padding: 2px;
}

.logo-container {
  position: relative;
  transition: all 0.2s ease-in-out;
}

.logo-container:hover {
  transform: scale(1.1);
}

#cart-logo {
  width: 40px;
  border-radius: 50%;
  filter: invert(1);
  cursor: pointer;
}

.total-quantity {
  position: absolute;
  bottom: 0;
  font-size: 13px;
  border-radius: 50%;
  border: 1px solid black;
  background-color: crimson;
  color: whitesmoke;
  width: 13px;
  height: 13px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.pokemon-detail {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.pokemon-detail h2,
h3,
h4 {
  margin: 10px;
}

.pokemon-detail h2 {
  margin-top: 25px;
}

.poke-image-detail {
  width: 200px;
  height: auto;
}

.cart-contanier {
  position: fixed;
  top: 96px;
  right: 0;
  background-color: whitesmoke;
  width: 30%;
  height: calc(100vh - 96px);
  max-height: 600px;
  border: 1px solid black;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.cart-inner-contanier {
  height: 300px;
  overflow-y: auto;
}

.cart-item-wrapper {
  display: flex;
}

.input-cart {
  width: 10px;
}

.cart-buttons-wrapper {
  display: flex;
  justify-content: space-around;
  width: 100%;
}

.cart-product-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100px;
}

.cart-product-wrapper h4 {
  margin: 5px;
}

#number-container {
  padding: 2px 10px;
}

.cart-button {
  width: 25px;
  font-weight: bold;
  font-size: 20px;
  background-color: var(--secondary-light);
  color: inherit;
  border: none;
  padding: 0;
  cursor: pointer;
  outline: inherit;
}

#cart-title {
  font-size: x-large;
  margin-top: 0px;
}

#total-amount {
  font-size: 19px;
}

#checkout-button,
#close-button {
  width: 60%;
  padding: 0.5em;
  font-weight: bold;
  letter-spacing: 0.1em;
}

#close-button {
  background-color: tomato;
  color: whitesmoke;
}

#close-button:hover {
  background-color: rgb(250, 62, 28);
  cursor: pointer;
}

#checkout-button {
  background-color: rgb(0, 71, 0, 0.6);
  cursor: pointer;
  color: whitesmoke;
}

#checkout-button:hover {
  background-color: rgba(18, 141, 18, 0.6);
}

.pokemon-item-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.add-to-cart-button {
  transition: all 0.2s ease-in-out;
  margin: 6px;
  background-color: var(--primary-dark);
  padding: 1px 20px;
  color: var(--primary-light);
}

.add-to-cart-button:hover {
  background-color: var(--primary-light);
  color: var(--primary-dark);
  font-weight: bold;
}

.info-container {
  width: 80%;
  margin: 0 auto;
  line-height: 1.6;
}

#close-modal-button {
  background-color: white;
  border-radius: 70%;
}

#close-modal-button:hover {
  background-color: rgba(0, 0, 0, 0.1);
}
